/**
 * @file 确认按钮
 * @author Yangholmes 2025-03-30
 */

import { defineComponent, PropType, ref, toRefs } from 'vue';

import { NButton, NPopconfirm } from 'naive-ui';

export default defineComponent({
  name: 'DeleteConfirmButton',
  props: {
    type: {
      type: String as PropType<InstanceType<typeof NButton>['type']>,
      default: 'default'
    },
    confirmText: {
      type: String,
      default: '确定删除吗？'
    },
    secondary: {
      type: Boolean,
      default: false
    },
    circle: {
      type: Boolean,
      default: false
    },
    onConfirm: {
      type: Function as PropType<() => void>
    },
    onCancel: {
      type: Function as PropType<() => void>
    },
  },
  setup(props, { slots }) {
    const isShow = ref(false);

    const onShow = () => {
      isShow.value = true;
    };

    const onHide = () => {
      isShow.value = false;
      props.onCancel?.();
    };

    const onConfirm = () => {
      isShow.value = false;
      props.onConfirm?.();
    };

    return () => <>
      <NPopconfirm
        showIcon={false}
        show={isShow.value}
        onClickoutside={onHide}
      >
        {{
          trigger: () => <NButton secondary={props.secondary} circle={props.circle} type={props.type} onClick={onShow}>
            {slots.default?.()}
          </NButton>,
          action: () => <>
            <NButton size="small" onClick={onHide}>算了</NButton>
            <NButton size="small" type="error" onClick={onConfirm}>删</NButton>
          </>,
          default: () => <>{props.confirmText}</>
        }}
      </NPopconfirm>
    </>;
  }
});
